.greetingForm {}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
    margin-bottom: 10px;
}

.error {
    color: #C00;

    font-family: "Montserrat";
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.input {
    width: 372px;
    height: 36px;
    border-radius: 3px;
    border: 1px solid #D1D1D1;
    color: #000;
    font-family: "Montserrat";
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding: 8px 12px;
}

.input:focus {}

.errorInput {
    border: 1px solid red;
}

.button {
    height: 36px;
    padding: 5px 24px;
    color: var(--light-140, #FFF);
    font-family: "Montserrat";
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    border-radius: 3px;
    background: var(--primary-100, #06C);
    border: none;
    cursor: pointer
}

.button:focus {}

.button:active {}

.button:disabled {}

.text {
    color: rgba(0, 0, 0, 0.50);
    font-family: "Montserrat";
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 9px;
}

.greeting {
    color: #000;
    font-family: "Montserrat";
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}